import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PopoverController } from '@ionic/angular';
import { Select, Store } from '@ngxs/store';
import { Observable, Subject } from 'rxjs';
import { takeUntil, filter, tap } from 'rxjs/operators';
import { MenuItemOptions, MenuObject } from 'src/app/interfaces/content';
import { LinkService } from 'src/app/services/link.service';
import { ToggleHelpIsActive } from 'src/app/store/actions/content.actions';
import { ToggleMessagesIsActive } from 'src/app/store/actions/message.actions';
import { MenuState } from 'src/app/store/state/menu.state';

@Component({
  selector: 'mu-menu-popover',
  templateUrl: './mu-menu-popover.component.html',
  styleUrls: ['./mu-menu-popover.component.scss'],
})
export class MuMenuPopoverComponent implements OnInit, OnDestroy {

  private ngUnsubscribe = new Subject();
  public items: MenuObject[];
  private defaultNavigationStyle: string = '_blank';
  constructor(
    public popoverController: PopoverController,
    private router: Router,
    private store: Store,
    private linkService: LinkService
  ) {}


  @Select(MenuState.selectedMenu)
  selectedMenuStream: Observable<MenuObject[]>;

  ngOnInit() {
    this.listenToSelectedMenuChanges();
  }
  private listenToSelectedMenuChanges(): void{

    this.selectedMenuStream
      .pipe(
        takeUntil(this.ngUnsubscribe),
        filter((value) => !!value),
        tap((menuObjects) => {
          this.items = menuObjects;
        })
      )
      .subscribe();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.unsubscribe();
  }

  async navigate(route: string | string[] , options: MenuItemOptions = {external: false,attributes: null}){
    await this.popoverController.dismiss();
    if(options.external && !Array.isArray(route)){
      // OPEN LINK
      this.linkService.openUrl(route, options);
    } else {
      if(!Array.isArray(route)) {
        route = [route];
      }
      this.router.navigate(route);
    }
  }
  async inbox(){
    await this.popoverController.dismiss();
    this.store.dispatch(new ToggleMessagesIsActive());
  }
  async help(){
    await this.popoverController.dismiss();
    this.store.dispatch(new ToggleHelpIsActive());
  }
  async logout() {
    await this.popoverController.dismiss();
    this.router.navigate(['/logout']);
  }


}
