import { Component, Input } from '@angular/core';

@Component({
  selector: 'mu-attention-label',
  styleUrls: ['./mu-attention-label.component.scss'],
  templateUrl: './mu-attention-label.component.html',
})
export class MuAttentionLabelComponent {
  @Input() deadline: string;
  dateIsApproaching: boolean;

  constructor() {}

  ngOnChanges() {
    if (this.deadline) {
      const deadline = new Date(this.deadline).getTime();
      const now = Date.now();
      const fourWeeks = 1000 * 60 * 60 * 24 * 7 * 4;
      if (deadline - now < fourWeeks) {
        this.dateIsApproaching = true;
      }
    }
  }
}
