import { Component, Input, OnInit } from '@angular/core';
import universityLogoFull from 'src/assets/logo_full.svg';
import universityLogoSmall from 'src/assets/logo_small.svg';

@Component({
  selector: 'mu-university-logo',
  styleUrls: ['./mu-university-logo.component.scss'],
  templateUrl: './mu-university-logo.component.html',
})
export class MuUniversityLogo implements OnInit {
  @Input() variant: 'full' | 'small' = 'full';
  universityLogo: string;

  constructor() {}

  ngOnInit() {
    switch (this.variant) {
      case 'full': {
        return (this.universityLogo = universityLogoFull);
      }
      default:
        this.universityLogo = universityLogoSmall;
    }
  }
}
