import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'mu-native-button',
  styleUrls: ['./native-button.component.scss'],
  templateUrl: './native-button.component.html',
})
export class MuNativeButtonComponent implements OnInit {
  @Input() noBackground: boolean;
  @Input() noPadding: boolean;
  @Input() caretDirection: 'left' | 'right';
  @Output() onClick = new EventEmitter();

  constructor() {}

  ngOnInit() {}

  clickHandler() {
    this.onClick.emit();
  }
}
