import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { Store } from '@ngxs/store';
import { Language, LanguageCode } from 'src/app/enums/language';
import { SwitchContentView, SwitchHelp, SwitchMenu } from 'src/app/store/actions/content.actions';
import { SetLanguageCode } from 'src/app/store/actions/participant.actions';
import { SwitchStatusTexts } from 'src/app/store/actions/status-texts.actions';
import { CombinedStateModel } from 'src/app/store/state';

@Component({
  selector: 'mu-language-popover',
  styleUrls: ['./mu-language-popover.component.scss'],
  templateUrl: './mu-language-popover.component.html',
})
export class MuLanguagePopoverComponent implements OnInit {
  languages: {
    value: LanguageCode;
    label: Language;
  }[] = [];

  constructor(private ctrl: PopoverController, private store: Store) {}

  ngOnInit() {
    this.initLanguages();
  }

  initLanguages() {
    this.languages.push(
      ...[
        { value: LanguageCode.NL, label: Language.NL },
        { value: LanguageCode.EN, label: Language.EN },
      ]
    );
  }

  setLanguageCode(lang: LanguageCode) {
    const { participant } = this.store.snapshot() as CombinedStateModel;
    if (lang !== participant.languageCode) {
      this.store.dispatch(new SetLanguageCode(lang));
      this.store.dispatch(new SwitchContentView());
    }
    this.ctrl.dismiss();
  }
}
