import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import {
  faCheck,
  faExclamation,
  faHourglassStart,
  faLock,
  faSquare,
  IconDefinition,
} from '@fortawesome/free-solid-svg-icons';
import { StatusText } from 'src/app/models/student-audit';
import { PlatformService } from 'src/app/services/platform.service';

@Component({
  selector: 'mu-student-audit',
  styleUrls: ['./mu-student-audit.component.scss'],
  templateUrl: './mu-student-audit.component.html',
})
export class MuStudentAuditComponent implements OnInit {
  @Input() isActive: boolean;
  @Input() isDisabled: boolean;
  @Input() isLocked: boolean;
  @Input() statusText: StatusText;
  @Input() id: string;
  @Input() label: string;
  @Output() onClick = new EventEmitter();
  iconData: {
    class?: string;
    src?: IconDefinition;
  } = {};
  faLock: IconDefinition = faLock;
  isNativeView: boolean;

  constructor(private platform: PlatformService) {}

  ngOnInit() {
    this.isNativeView = !this.platform.isWeb();
    this.renderStatusIcon();
  }

  private renderStatusIcon() {
    switch (this.statusText) {
      case StatusText.WAIT_FOR_STUDENT: {
        return (this.iconData = {
          class: 'open-checkbox-icon',
          src: faSquare,
        });
      }
      case StatusText.WAIT_FOR_UM: {
        return (this.iconData = {
          class: 'other-icon-type lock-icon-type',
          src: faLock,
        });
      }
      case StatusText.IN_PROCESS: {
        return (this.iconData = {
          class: 'hourglass-icon',
          src: faHourglassStart,
        });
      }
      case StatusText.NOT_COMPLETED: {
        return (this.iconData = {
          class: 'other-icon-type',
          src: faExclamation,
        });
      }
      case StatusText.COMPLETED: {
        return (this.iconData = {
          class: 'other-icon-type',
          src: faCheck,
        });
      }
      default:
        console.warn(
          'audit has unexpected value. No icon loaded: ',
          this.statusText
        );
    }
  }

  clickHandler() {
    this.onClick.emit();
  }
}
