import { Component, OnInit } from '@angular/core';
import { PopoverController, } from '@ionic/angular';
import { Store } from '@ngxs/store';
import { SetShowAuditContent } from 'src/app/store/actions/student-audits.actions';
import { SetSelectedGuid} from 'src/app/store/actions/student-admissions.actions';
import { PlatformService } from 'src/app/services/platform.service';

@Component({
  selector: 'mu-admissions-popover',
  templateUrl: './mu-admissions-popover.component.html',
  styleUrls: ['./mu-admissions-popover.component.scss'],
})

export class MuAdmissionsPopoverComponent implements OnInit {
  site: any;
  selectOptions: any;
  selectedYears: any;

  constructor(
    public popoverController: PopoverController,
    private store: Store,
    private platform: PlatformService,
  ) {}

  ngOnInit() {
  }

  setSelectedGuid(educationName: any): void {
    const [selectedOption] = this.selectOptions.filter(
      ({ label }) => label === educationName
    );
    this.store.dispatch(new SetSelectedGuid(selectedOption.guid, selectedOption.status, selectedOption.locked, selectedOption.contextColor));
    this.popoverController.dismiss();
    if(this.platform.isWeb()){
      this.store.dispatch(new SetShowAuditContent(true));
    }
  }
  keydown(e: KeyboardEvent, educationName: any){
    const key = e.key;
    if(key === " " || key === "Enter"){
      this.setSelectedGuid(educationName);
    }
  }
}
