import { Component, Input, OnChanges } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { FaIconComponent } from '@fortawesome/angular-fontawesome';
import { faExternalLinkAlt, IconDefinition } from '@fortawesome/free-solid-svg-icons';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

@Component({
  selector: 'mu-raw-html',
  template: `<div
    class="drupal-content"
    [innerHTML]="dangerouslySetHTML"
  ></div>`,
  styleUrls: ['./mu-raw-html.component.scss'],
})
export class MuRawHTMLComponent implements OnChanges {
  @Input() unsafeHtml: string;
  @Input() variant?: 'header';
  linkIcon: IconDefinition = faExternalLinkAlt;
  dangerouslySetHTML: SafeHtml;

  constructor(public sanitizer: DomSanitizer, private iab: InAppBrowser) {}

  ngOnChanges() {
    let html = this.unsafeHtml || '';
    if (html) {
      switch (this.variant) {
        case 'header': {
          html = this.addAccessibility(`<h1>${this.unsafeHtml}</h1>`);
          break;
        }
        default:
        html = this.addAccessibility(html);
      }
    }
    this.dangerouslySetHTML = this.sanitizer.bypassSecurityTrustHtml(html);
  }
  parseHTML(str) {
    var parser = new DOMParser();
    var doc = parser.parseFromString(str, 'text/html');
    return doc.body;
  }
  addAccessibility(unsafeHtml: string){
    // TODO:: ADD ACCESSIBILITY STRUCTS IF POSSIBLE
    const parsedHtml = this.parseHTML(unsafeHtml);
    if(parsedHtml){
      // ADD TAB INDEXES
      let tabIndex: number = 0;
      const abbrs = Array.prototype.slice.call(parsedHtml.querySelectorAll('abbr'));
      // @ts-ignore
      for(const abbr of abbrs){
        abbr.tabIndex = tabIndex;
        ++tabIndex;
      }

      
      return parsedHtml.outerHTML;
    } else {
      return unsafeHtml;
    }
  }
}
